//GRID

$grid-width: 114rem;
$gutter-vertical: 8rem;
$gutter-horizontal: 6rem;

$default-font-size: 1.6rem;

@mixin clearfix {
  &::after {
    content: '';
    display: table;
    clear: both;
  }
}

@mixin center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

//Media Query/////
/// 0  -600 px

// 600 -900 px

// 900px - 1200px

//1200-1800px normal

// 1800px -

// @mixin respose-phone {
//   @media (max-width: 600px) {
//     @content;
//   }
// }

@mixin respose($breakpoint) {
  @if $breakpoint == phone {
    @media (max-width: 32.5em) {
      //600 /16px

      @content;
    }
  }

  // @if $breakpoint == 800px {
  //   @media (max-width: 50em) {

  //     @content;
  //   }
  // }

  @if $breakpoint == tab-port {
    @media (max-width: 56.25em) {
      @content;
    }
  }
  @if $breakpoint == tab-land {
    @media (max-width: 75em) {
      @content;
    }
  }
  @if $breakpoint == big-desktop {
    @media (min-width: 112.5em) {
      @content;
    }
  }
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  // font-size: 10px;
  font-size: 62.5%;

  @include respose(tab-land) {
    font-size: 56.25%;
  }

  @include respose(tab-port) {
    font-size: 50%;
  }
  // @include respose(800px) {
  //   font-size: 40%;
  // }

  @include respose(phone) {
    font-size: 30%;
  }

  @include respose(big-desktop) {
    font-size: 75%;
  }
}

body {
  /* margin: 0; */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-family: 'Lato', sans-serif;
  font-weight: 400;
  /* font-size: 16px; */
  line-height: 1.7;
  color: $color-grey-dark;
  padding: 3rem;
  box-sizing: border-box;

  @include respose(tab-port) {
    padding: 0;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
