@import '../index.scss';
.navigation {
  .checkbox {
    display: none;
  }
  .button {
    background-color: $color-white;
    width: 7rem;
    height: 7rem;
    position: fixed;
    top: 6rem;
    right: 6rem;
    border-radius: 50%;
    z-index: 2000;
    box-shadow: 0rem 1rem 3rem rgba($color-black, 0.1);
    text-align: center;
    cursor: pointer;

    // @include respose(tab-port) {
    //   top: 4.5rem;
    //   right: 4.5rem;
    // }

    // @include respose(tab-phone) {
    //   top: 3rem;
    //   right: 3rem;
    // }
  }
  .background {
    height: 6rem;
    width: 6rem;
    border-radius: 50%;
    position: fixed;
    top: 6.5rem;
    right: 6.5rem;
    background-image: radial-gradient(
      $color-primary-light,
      $color-primary-dark
    );
    z-index: 1000;
    transition: transform 0.8s cubic-bezier(0.66, 0.89, 0.23, 1.02);

    // @include respose(tab-port) {
    //   top: 4.5rem;
    //   right: 4.5rem;
    // }
  }
  .nav {
    height: 100vh;
    width: 100%;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1500;
    text-align: center;

    opacity: 0;
    width: 0;
    transition: all 0.8s;
  }

  .list {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    list-style: none;
    width: 100%;
  }

  .item {
    margin: 1rem;
  }

  .link {
    &:link,
    &:visited {
      display: inline-block;
      font-size: 3rem;
      font-weight: 300;
      color: $color-white;
      text-transform: uppercase;
      text-decoration: none;
      background-image: linear-gradient(
        120deg,
        transparent 0%,
        transparent 50%,
        $color-white 50%
      );
      background-size: 230%;
      transition: all 0.4s;
    }

    &:hover,
    &:active {
      background-position: 100%;
      color: $color-primary;
      transform: translateX(1rem);
    }
  }

  .checkbox:checked ~ .background {
    transform: scale(90);
  }

  .checkbox:checked ~ .nav {
    opacity: 1;
    width: 100%;
  }

  .icon {
    position: relative;
    margin-top: 3.5rem;
    &,
    &::before,
    &::after {
      width: 3rem;
      height: 2px;
      background-color: $color-grey-dark;
      display: inline-block;
    }

    &::before,
    &::after {
      content: '';
      position: absolute;
      left: 0;
      transition: all 0.2s;
    }

    &::before {
      top: -0.8rem;
    }
    &::after {
      top: 0.8rem;
    }
  }

  .button:hover .icon::after {
    top: 1rem;
  }

  .checkbox:checked + .button .icon {
    background-color: transparent;
  }

  .checkbox:checked + .button .icon::before {
    top: 0;
    transform: rotate(135deg);
  }

  .checkbox:checked + .button .icon::after {
    top: 0;
    transform: rotate(-135deg);
  }
}
