@import '../index.scss';

.margin {
  margin-bottom: 10rem;
}

.section_about {
  background-color: $color-grey-light-1;
  padding: 25rem 0;
  margin-top: -20vh;
}

.button_text {
  &:link,
  &:visited {
    font-size: $default-font-size;
    color: $color-primary;
    display: inline;
    text-decoration: none;
    border-bottom: 1px solid $color-primary;
    padding: 0.3rem;
  }

  &:hover {
    background-color: $color-primary;
    color: $color-white;
    box-shadow: 0 1rem 2rem rgba($color-black, 0.15);
    transform: translateY(-2px);
    transition: all 0.4s;
  }
  &:active {
    box-shadow: 0 0.5rem 1rem rgba($color-black, 0.15);
    transform: translateY(0);
  }
}

.composition {
  position: relative;
  img {
    width: 55%;
    box-shadow: 0 1.5rem 4rem rgba($color-black, 0.4);
    // border-radius: 2px;
    position: absolute;
    z-index: 10;
    transition: all 0.2s;
    outline-offset: 2rem;

    @include respose(tab-port) {
      float: left;
      position: relative;
      width: 33.33333%;
      box-shadow: 0 1.5rem 3rem rgba($color-black, 0.2);
      margin-top: 5rem;
    }

    &:hover {
      transform: scale(1.05) translateY(-0.5rem);
      box-shadow: 0 2.5rem 4rem rgba($color-black, 0.5);
      z-index: 20;
      // outline: 1.5rem solid $color-primary;
    }
  }
  .photo_1 {
    left: 0;
    top: -2rem;

    @include respose(tab-port) {
      top: 0;
      transform: scale(1.2);
    }
  }
  .photo_2 {
    right: 0;
    top: 2rem;
    @include respose(tab-port) {
      top: -1rem;
      transform: scale(1.3);
      z-index: 100;
    }
  }
  .photo_3 {
    left: 20%;
    top: 10rem;
    @include respose(tab-port) {
      top: 0;
      left: 0;
      transform: scale(1.2);
    }
  }
  &:hover img:not(:hover) {
    transform: scale(0.95);
  }
}
