@import '../../index.scss';

.row {
  max-width: 114rem;

  margin: 0 auto;

  &:not(:last-child) {
    margin-bottom: 8rem;
  }
  @include clearfix;

  @include respose(tab-port) {
    display: grid;
    grid-template-columns: 1fr;
    width: 100%;
    // text-align: center;
    // padding: 0;
    place-items: center;
  }

  .col_1_of_2 {
    width: calc((100% - #{$gutter-horizontal}) / 2);

    float: left;

    &:not(:last-child) {
      margin-right: $gutter-horizontal;
    }

    @include clearfix;

    @include respose(tab-port) {
      &:not(:last-child) {
        margin-right: 0;
      }
    }
  }

  .col_1_of_3 {
    width: calc((100% - 2 * #{$gutter-horizontal}) / 3);

    float: left;

    &:not(:last-child) {
      margin-right: $gutter-horizontal;
    }
  }

  .col_2_of_3 {
    width: calc(
      2 * (100% - 2 * #{$gutter-horizontal}) / 3 + #{$gutter-horizontal}
    );

    float: left;

    &:not(:last-child) {
      margin-right: $gutter-horizontal;
    }
  }
}
